html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  border: 0;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

.testing {
  display: flex;
  width: 71.42857%;
}

body {
  background: #c3c3c3;
  color: black;
  background: -webkit-radial-gradient(circle, white 50%, #c3c3c3);
  /* Safari */
  background: -o-radial-gradient(circle, white 50%, #c3c3c3);
  /* Opera 11.6 to 12.0 */
  background: -moz-radial-gradient(circle, white 50%, #c3c3c3);
  /* Firefox 3.6 to 15 */
  background: radial-gradient(circle, white 50%, #c3c3c3);
  /* Standard syntax */
}

.block {
  width: 100%;
  text-align: center;
}

.block__image1 {
  display: block;
  height: 500px;
  margin: auto;
}

h1 {
  font-size: 24px;
}

p {
  font: 16px serif;
  color: #4a4a4a;
}

code {
  font-family: monospace;
  padding: 0 5px;
  color: white;
  background: black;
}

div.container {
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
}

div.container::after {
  display: table;
  clear: both;
  content: '';
}

div.container .section1 {
  display: block;
  float: left;
  width: 48.93617%;
  margin-right: 2.12766%;
  margin-left: 34.04255%;
}

div.container .section1:last-child {
  margin-right: 0;
}

@media screen and (max-width: 768px) {
  div.container .section1 {
    display: block;
    float: left;
    width: 58.97436%;
    margin-right: 2.5641%;
    margin-left: 30.76923%;
  }

  div.container .section1:last-child {
    margin-right: 0;
  }
}

@media screen and (max-width: 480px) {
  div.container .section1 {
    display: block;
    float: left;
    width: 100%;
    margin-right: 6.66667%;
    margin-left: 26.66667%;
  }

  div.container .section1:last-child {
    margin-right: 0;
  }
}