//CSS Reset
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
// Global

.testing {
  display: flex;
  width: percentage(5/7);
  // Percentage is a Sass function that will be evaluated to CSS when compiled
}
body {
    color: black;
    background-color: #c3c3c3;
    background: -webkit-radial-gradient(circle, white 50%, #c3c3c3); /* Safari */
  background: -o-radial-gradient(circle, white 50%, #c3c3c3); /* Opera 11.6 to 12.0 */
  background: -moz-radial-gradient(circle, white 50%, #c3c3c3); /* Firefox 3.6 to 15 */
  background: radial-gradient(circle, white 50%, #c3c3c3); /* Standard syntax */
}
